import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, Tabs, Tab, IconButton, Drawer, List, ListItem, ListItemText, useMediaQuery, useTheme } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Link, useLocation } from 'react-router-dom';

const Navigation = () => {
   const [drawerOpen, setDrawerOpen] = useState(false);
   const theme = useTheme();
   const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
   const location = useLocation();

   const toggleDrawer = (open) => (event) => {
     setDrawerOpen(open);
   };

   const menuItems = [
     { label: 'Home', path: '/' },
     { label: 'Active Business Search', path: '/active-business-search' },
     { label: 'Business Profit Calculator', path: '/business-profit-calculator' },
     { label: 'Copyright Note', path: '/copyright-note' }
   ];

   const currentTab = menuItems.findIndex(item => item.path === location.pathname);

   return (
     <AppBar position="static">
       <Toolbar>
         <Typography variant="h6" sx={{ flexGrow: 1 }}>
           LiquorBizConnect
         </Typography>
         {isMobile ? (
           <>
             <IconButton
               edge="start"
               color="inherit"
               aria-label="menu"
               onClick={toggleDrawer(true)}
             >
               <MenuIcon />
             </IconButton>
             <Drawer 
               anchor="right" 
               open={drawerOpen} 
               onClose={toggleDrawer(false)}
               sx={{
                  "& .MuiListItem-root": {
                     color: "white", // White text for drawer items
                  }
               }}
               >
               <List sx={{ backgroundColor: 'primary.main', height: '100%' }}>
                  {menuItems.map((item) => (
                     <ListItem 
                     button 
                     key={item.label} 
                     component={Link} 
                     to={item.path}
                     onClick={toggleDrawer(false)}
                     sx={{
                        color: "white",
                        '&:hover': {
                           backgroundColor: 'primary.dark', // Optional: add hover effect
                        }
                     }}
                     >
                     <ListItemText primary={item.label} />
                     </ListItem>
                  ))}
               </List>
             </Drawer>

           </>
         ) : (
            <Tabs 
            value={currentTab} 
            variant="standard"
            scrollButtons="auto"
            sx={{
              // Styling for the entire Tabs component
              "& .MuiTab-root": {
                color: "white", // Default tab text color
                textTransform: "none", // Optional: prevents uppercase transformation
              },
              "& .Mui-selected": {
                color: "white", // Selected tab text color
                fontWeight: "bold", // Optional: make selected tab bold
              },
              "& .MuiTabs-indicator": {
                backgroundColor: "white", // White underline for selected tab
              }
            }}
          >
            {menuItems.map((item) => (
              <Tab 
                key={item.label} 
                label={item.label} 
                component={Link} 
                to={item.path} 
              />
            ))}
          </Tabs>
          
         )}
       </Toolbar>
     </AppBar>
   ); 
};

export default Navigation;
